import { useCallback } from 'react';

// use dimension
const UseLabels = () => {
  // get label
  const getLabel = useCallback((label: string) => {
    switch (label) {
      case 'atomicMass':
        return {
          name: 'Atomic Mass',
          label: 'Masa Atmica'
        };
      case 'atomicNumber':
        return {
          name: 'Atomic Number',
          label: 'Numero Atmico'
        };
      case 'electronegativity':
        return {
          name: 'Electro-Negativity',
          label: 'Electronegatividad'
        };
      case 'name':
        return {
          name: 'Name',
          label: 'Nombre'
        };
      case 'electronicConfiguration':
        return {
          name: 'E Config',
          label: 'Config. Electronica'
        };
      case 'ionizationEnergy':
        return {
          name: 'Ionization Energy',
          label: 'Energa Ionizacin'
        };
      case 'oxidationStates':
        return {
          name: 'Oxidation States',
          label: 'Estados Oxidacin'
        };
      case 'boilingPoint':
        return {
          name: 'Boiling Point',
          label: 'Punto Ebullicin'
        };
      case 'bondingType':
        return {
          name: 'Bonding Type',
          label: 'Tipo Vinculacin'
        };
      case 'density':
        return {
          name: 'Density',
          label: 'Densidad'
        };
      case 'electronAffinity':
        return {
          name: 'Electron Affinity',
          label: 'Afinidad Electronica'
        };
      case 'period':
        return {
          name: 'Period',
          label: 'Perodo'
        };
      case 'groupBlock':
        return {
          name: 'Group Block',
          label: 'Bloque Grupo'
        };
      case 'ionRadius':
        return {
          name: 'Ion Radius',
          label: 'Radio de Iones'
        };
      case 'standardState':
        return {
          name: 'Standard State',
          label: 'Estado Estndar'
        };
      case 'meltingPoint':
        return {
          name: 'Melting Point',
          label: 'Punto Fusion'
        };
      default: 
        return false;
    }
  }, []);

  // get label block
  const getLabelBlock = useCallback((label: string) => {
    switch (label) {
      case 'atomicMass':
        return {
          name: 'Atomic Mass',
          label: 'Masa Atmica'
        };
      case 'atomicNumber':
        return {
          name: 'Atomic Number',
          label: 'Numero Atmica'
        };
      case 'electronegativity':
        return {
          name: 'Electro-Negativity',
          label: 'Electronegatividad'
        };
      case 'name':
        return {
          name: 'Name',
          label: 'Nombre'
        };
      case 'electronicConfiguration':
        return {
          name: 'E Config',
          label: 'Config. Electronica'
        };
      case 'ionizationEnergy':
        return {
          name: 'Ionization Energy',
          label: 'Energa Ionizacin'
        };
      case 'oxidationStates':
        return {
          name: 'Oxidation States',
          label: 'Estados Oxidacin'
        };
      default: 
        return false;
    }
  }, []);

  return {
    getLabel,
    getLabelBlock
  };
};

export default UseLabels;